//     })
//     .listen('MessagePosted', (e) => {
//       // this.messages.push({
//       //   message: e.message.message,
//       //   user: e.user
//       // })
//     })
// }

const app = new Vue({
  el: '#app',
  // 
  router,
  // vuex store
  store,

  computed: {
    ...mapState({
      isLoading: state => state.isLoading
    }),

    ...mapGetters([
      'accounts',
      'user'
    ])
  },

  data: {
    loadingInstance: null
  },

  methods: {
    success (msg) {
      this.$message({
        message: msg,
        type: 'success',
        duration: 1000
      })
    },

    warning (msg) {
      this.$message({
        message: msg,
        type: 'warning',
        duration: 1000
      })
    },

    error (msg) {
      this.$message({
        message: msg,
        type: 'error',
        duration: 2000
      })
    }
  },

  watch: {
    'isLoading': function (value) {
      if (value) {
        this.loadingInstance = Loading.service({
          text: 'Loading',
          customClass: 'my-loading-mask'
        })
      } else {
        this.loadingInstance && this.loadingInstance.close()
      }
    }
  },

  destroy () {
    this.loadingInstance = null
  }
})



